import React, { useState, useEffect, useContext, useCallback } from 'react';
import Filter from 'bad-words';
import { Modal } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Axios from 'axios';
import { CreateRounded, DeleteRounded } from '@material-ui/icons';
import { AuthContext } from '../contexts/auth_context';
import Notification from '../lib/notification';

import DropdownMenu, { DropdownItem } from './dropdown';

import { getApiLink } from '../lib/env.lib';
import { ReactComponent as Loader } from '../images/loader.svg';

import { ReactComponent as Menu } from '../icons/ellipse.svg';

const filter = new Filter();

const formatDate = (dateStr) => {
  const date = new Date(dateStr);
  const seconds = Math.floor((new Date() - date) / 1000);

  let interval = Math.floor(seconds / 31536000);

  if (interval > 1) {
    return `${interval} years ago`;
  }
  interval = Math.floor(seconds / 2592000);
  if (interval > 1) {
    return `${interval} months ago`;
  }
  interval = Math.floor(seconds / 86400);
  if (interval > 1) {
    return `${interval} days ago`;
  }
  interval = Math.floor(seconds / 3600);
  if (interval > 1) {
    return `${interval} hours ago`;
  }
  interval = Math.floor(seconds / 60);
  if (interval > 1) {
    return `${interval} minutes ago`;
  }
  if (Math.floor(seconds) === 0) {
    return 'now';
  }
  return `${Math.floor(seconds)} seconds ago`;
};

const Comments = (props) => {
  const { isLoggedIn, auth_token, user_id, user_fullname } = useContext(
    AuthContext,
  );

  const [modalDisp, setModalDisp] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [comments, setComments] = useState([]);
  const [value, setValue] = useState('');
  const [deletionId, setDeletionId] = useState('');
  const [disabled, setDisabled] = useState(true);

  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(30);

  const initComment = useCallback(() => {
    if (props.articleId !== '') {
      Axios.get(
        `${getApiLink()}/api/v1/getComments/${
          props.articleId
        }/${page}/${limit}`,
      ).then((response) => {
        if (response.data.success) {
          let c = [];
          if (page !== 0) c = [...comments];
          setComments([...c, ...response.data.message.data]);
        }
        console.log(response);
      });
    }
  }, [limit, page, props.articleId]);

  useEffect(() => {
    initComment();
  }, [initComment]);

  useEffect(() => {
    const v = value;
    if (v.trim().split('&nbsp').join() !== '') setDisabled(false);
    else setDisabled(true);
  }, [value]);

  const handleChange = (e) => {
    setValue(e.target.value);
  };

  const handleSubmit = () => {
    // let c = comments
    const v = value;
    if (isLoggedIn) {
      if (v.trim().split('&nbsp').join() !== '') {
        if (props.articleId !== '' && props.articleId != null)
          Axios.post(`${getApiLink()}/api/v1/addComment`, {
            xAuthToken: auth_token,
            article_id: props.articleId,
            comment: filter.clean(value),
          })
            .then((res) => {
              setValue('');
              setPage(0);
              initComment();
            })
            .catch((e) => {
              console.log(e);
            });
        // setValue("")
        // setComments(c)
      }
    } else {
      Notification.error('You need to be logged in to comment!');
    }
  };

  const handleDelete = () => {
    if (deletionId !== '')
      Axios.delete(`${getApiLink()}/api/v1/deleteComment/`, {
        data: {
          xAuthToken: auth_token,
          article_id: props.articleId,
          comment_id: deletionId,
        },
      })
        .then((r) => {
          if (r.data.success) {
            const c = comments;
            for (let i = 0; i < c.length; i++) {
              if (c[i].comment.comment_id === deletionId) {
                c.splice(i, 1);
                i--;
                break;
              }
            }
            setDeletionId('');
            setComments(c);
          }
        })
        .catch((e) => {
          setDeletionId('');
          console.log(e);
        });
  };

  const handleReset = () => {
    setValue('');
  };

  return (
    <>
      <h5>Comment:</h5>

      <textarea
        name="new-comment"
        id="comments-textar"
        className="w-100 form-control mt-2"
        rows="2"
        placeholder="Tell me what you think 😊"
        onChange={handleChange}
        value={value}
      />
      <button
        className="btn btn-primary mt-2 mr-2"
        onClick={handleSubmit}
        disabled={disabled}
        type="button"
      >
        COMMENT
      </button>

      {!disabled && (
        <button
          className="btn btn-secondary mt-2 red"
          onClick={handleReset}
          type="button"
        >
          RESET
        </button>
      )}

      <hr className="light mt-4" />

      <p className="mt-2 mb-3" id="comments">
        {comments.length} comments
      </p>

      {!isLoading &&
        comments
          .map((i) => {
            return (
              <div
                className="comment-container mb-3"
                key={i.comment.comment_id}
              >
                <div className="comment-block ml-2 mr-2 pt-1 pb-1">
                  <img
                    src={i.author_details[0].details.profile_image_link}
                    alt=""
                    className="d-inline-block cmt-icon"
                  />
                  <div className="d-inline-block cmt-name noselect ml-2">
                    <Link
                      to={`/@${i.author_details[0].details.username}`}
                      className="unset"
                    >
                      <strong style={{ cursor: 'pointer' }}>
                        {i.author_details[0].details.fullname}
                      </strong>
                    </Link>
                  </div>
                  <span className="noselect">・</span>
                  <div className="d-inline-block cmt-time noselect">
                    <time
                      dateTime={new Date(i.comment.commented_on).toISOString()}
                    >
                      {formatDate(i.comment.commented_on)}
                    </time>
                  </div>
                  {i.comment.user_id === user_id && (
                    <div className="float-right pr-3">
                      <DropdownMenu
                        title={<Menu className="action-icon" />}
                        autoClose
                        className="drop-btn dropdown-toggle_x btn_x"
                        containerClassName=" ml-auto float-right"
                        containerStyle={{
                          // marginRight: "4fr"
                          // float: "right"
                          cursor: 'pointer',
                        }}
                        menuStyle={{
                          transform:
                            window.innerWidth <= 576
                              ? 'translateX(-80%)'
                              : null,
                        }}
                      >
                        {/* <DropdownItem type="link" to="/settings">
                                            Settings
                                        </DropdownItem> */}
                        {/* <hr className="light p-0 m-0" /> */}

                        {/* <DropdownItem icon={<CreateRounded />} type="link" to={`/edit/${props.data.article_id}`}>Edit</DropdownItem>
                                        <hr className="light p-0 m-0" /> */}
                        <DropdownItem
                          className="logout"
                          icon={<DeleteRounded />}
                          onClick={() => {
                            // set active comment id for deletion
                            setDeletionId(i.comment.comment_id);
                            setModalDisp(true);
                          }}
                        >
                          Delete
                        </DropdownItem>
                      </DropdownMenu>
                    </div>
                  )}
                  <div className="cmt-content mt-1">{i.comment.comment}</div>
                </div>
              </div>
            );
          })
          .reverse()}

      {/* <br />
            <br /> */}

      {isLoading && (
        <>
          <div className="w-100 text-center">
            <Loader className="loader" />
          </div>
        </>
      )}

      <Modal
        show={modalDisp}
        onHide={() => {
          setModalDisp(false);
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title>Delete Comment</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Are you sure you want to delete this comment?
          <br />
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-secondary"
            onClick={() => {
              setModalDisp(false);
            }}
            type="button"
          >
            Close
          </button>
          <button
            className="btn btn-danger"
            onClick={() => {
              // deleteArticle(auth_token, props.data.article_id, () => {
              //     props.hideCard()
              // })
              setModalDisp(false);
              handleDelete();
            }}
            type="button"
          >
            Delete
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default Comments;
