import React, { useEffect, useState } from 'react';

import Axios from 'axios';
import { Helmet } from 'react-helmet';
import ReadMode from '../components/readMode';
import { getApiLink } from '../lib/env.lib';
import Page404 from './404';
import Comments from '../components/commentEngine';

// import { DiscussionEmbed } from 'disqus-react';
const ArticleReader = (props) => {
  // const instanceRef = useRef(null);
  let key = 0;
  const [data, setData] = useState(null);
  const [disp, setDisp] = useState(0);
  const [banner, setBanner] = useState('');
  const [title, setTitle] = useState('');
  useEffect(() => {
    Axios.post(`${getApiLink()}/api/v1/getArticleById`, {
      article_id: props.articleId,
    })
      .then((response) => {
        console.log(response);
        // alert(response)

        if (response.data.success) {
          if (response.data.message.data.content) {
            try {
              if (response.data.message.data.article_banner !== 'NA')
                setBanner(response.data.message.data.article_banner);

              setTitle(response.data.message.data.title);
              setData(JSON.parse(response.data.message.data.content));
              setDisp(1);
            } catch {
              setDisp(2);
            }
          }
        }
      })
      .catch((e) => {
        setDisp(2);
        try {
          if (e) {
            console.log({ resp: e.response.data });
            if (e.response.data.error.message !== null) {
              console.log(e);
            } else {
              // notyf.error("Error")
            }
          } else {
            // notyf.error("Error")
          }
        } catch (err) {
          console.log(err);
        }
      });
  }, []);

  return (
    <>
      <div
        style={{
          overflowY: 'hidden',
        }}
      >
        {banner !== 'NA' && banner !== '' && (
          <>
            <div className="w-100 show-xs show-sm hide-md preload_x">
              <div
                className=" text-center mb-2"
                style={{
                  height: '40vw',
                  width: '100vw',
                  overflow: 'hidden',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  // marginTop: '-15px',
                  // borderRadius: '16px',
                  // marginTop: "60px"
                }}
              >
                <div className="preload w-100 h-100 preload">
                  <img
                    src={banner}
                    className="preload_x"
                    alt="&nbsp;"
                    style={{
                      height: '100%',
                      width: 'auto',
                      // borderRadius: '16px',
                    }}
                  />
                </div>
              </div>
            </div>
          </>
        )}

        <div
          className="container"
          style={{
            overflowY: 'hidden',
          }}
        >
          <div className="row">
            <div className="col-lg-2" />
            <div className="col-lg-8">
              {banner !== 'NA' && banner !== '' && (
                <>
                  <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                    <img
                      className="preload"
                      src={banner}
                      alt="&nbsp;"
                      style={{
                        maxHeight: '20vw',
                        width: '100%',
                        height: '20vw',
                        borderRadius: '16px',
                        // marginTop: "60px"
                      }}
                    />
                  </div>
                </>
              )}

              <div className="m-4" />
              {disp === 1 && (
                <>
                  <Helmet>
                    <meta charSet="utf-8" />
                    <title>{title} - Devorzo</title>
                    <link
                      rel="canonical"
                      href={`https://devorzo.github.io/read/${props.articleId}`}
                    />
                  </Helmet>
                  <h1>{title}</h1>
                  <ReadMode key={(key++).toString()} data={data} />
                  <br />
                  <br />
                  <hr className="light" />
                  <Comments articleId={props.articleId} />
                </>
              )}
              {disp === 2 && (
                <>
                  <Page404 />
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ArticleReader;
